.search-page {
    color: #618b25; 
    background-color: #000000; 
    padding: 20px;
    font-family: 'JetBrains Mono', Courier, monospace; 
  }
  
  .search-title {
    margin-bottom: 20px;
    font-size: 24px;
  }
  
  input[type="text"] {
    background-color: #000000; 
    color: #618b25;
    border: 1px solid #74b474; 
    padding: 10px;
    width: 100%;
  }
  
  button[type="submit"] {
    background-color: #618b25; 
    color: #000000; 
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-family: 'Courier New', Courier, monospace;
  }
  
  button[type="submit"]:hover {
    background-color: #171b17; 
    color:rgb(131, 56, 127);
  }
  